.card{
    // position: relative;
    width: 300px;
    font-size: 24px;
    box-shadow: 8px 18px 15px -5px #ffe5e5;
    border-radius: 30px;
    background-color: aliceblue;

    @include mq(lg){
        width: 280px;
        font-size: 20px;
    }
    @include mq(md){
        width: 200px;
        font-size: 16px;
    }
    @include mq(sm){
        width: 130px;
        font-size: 8px;
    }
    
}

// .voicePlayButton{
//     position: absolute;
//     top: 7%;
//     right:10%;
// }

.TapText{
    display: none;
    @include mq(md){
        display: block;
    }
}

.cardImgContainer{
    position: relative;
    & img{
        scale: 1.2;
        &:hover{
            cursor: pointer;
            animation: pokemonAnimate 2s ease 0.3s 1;
        }
        @include mq(md){
            animation: pokemonAnimate 2s linear 2 ;
        }
    }
    
    & .hoverText{
        position: absolute;
        top: 50%;
        left:60%;
        transform: translate(-50% -50%);
        font-size: 10px;
        // font-family: ;
        display: none;
    }

    &:hover{
        .hoverText{
            display: block;
            @include mq(md){
                display: none;
            }
        }
    }
}
.close{
    border-radius: 70px;
    padding: 5px;
    background-color: skyblue;

    &:hover{
        cursor: pointer;
    }
}

